.toggler input {
  opacity: 0;
  display: none;
  overflow: hidden;
}

.toggler label {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border: rgba(0, 0, 0, 0);
  border-radius: 10px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.toggler label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.toggler input:checked + label {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  animation: switch_color .3s linear;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.toggler input:not(:checked) + label {
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.toggler input:checked + label:active {
  animation: none;
}

.toggler input:checked + label:after {
  animation: smooth_entry .3s linear;
  left: 21px;
}

.toggler input:not(:checked) + label:after {
  animation: smooth_exit .3s linear;
}

@keyframes smooth_entry {
  0% {
    left: 1px;
  }

  100% {
    left: 21px;
  }
}

@keyframes smooth_exit {
  0% {
    left: 21px;
  }

  100% {
    left: 1px;
  }
}

@keyframes switch_color {
  0% {
    background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
    box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
  }

  100% {
    background: linear-gradient(#4f2ee8 0%, #686868 100%);
    box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
  }
}

